@font-face {
  font-family: "gotham_prolight";
  src: url("../fonts/gothampro-light-webfont.woff2") format("woff2"), url("../fonts/gothampro-light-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-size: 16px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

input,
select,
textarea,
button {
  font-family: inherit;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.swiper-container {
  width: 100%;
  height: 100%;
}

.modal {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  top: 0;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1c1c1f;
}
.modal.open {
  visibility: visible;
  opacity: 1;
  transition-delay: 0s;
}
.modal-bg {
  background: #1c1c1f;
  width: 100%;
  height: 100%;
}
.modal-container {
  width: 100%;
  height: 100%;
  max-height: 625px;
  border-radius: 10px;
  position: relative;
  padding: 30px;
  display: flex;
  overflow: hidden;
}
.modal-container img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.modal-close {
  position: absolute;
  right: 15px;
  top: 15px;
  outline: none;
  appearance: none;
  color: #f2cc91;
  background: none;
  border: 0px;
  font-weight: bold;
  cursor: pointer;
}

.page {
  height: 100%;
  background-color: #1c1c1e;
}

.page__body {
  min-width: 0%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "gotham_proregular", "Arial", sans-serif;
  background-color: #1c1c1e;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  max-width: 1920px;
  display: grid;
  grid-template-columns: 85px 3.7fr 1fr;
  grid-template-rows: auto 1fr auto;
}
.page__body--inner {
  grid-template-columns: 85px 2.5fr 1fr;
  background-image: url("../images/item-bg.svg"), url("../images/item-bg-2.svg");
  background-repeat: repeat, no-repeat;
  background-size: cover, contain;
  background-attachment: fixed;
  background-position-x: center, right;
  background-position-y: top, bottom;
}
@media (max-width: 1023px) {
  .page__body--inner {
    grid-template-columns: 30px 2.5fr 1fr;
  }
}
@media (max-width: 1023px) {
  .page__body {
    grid-template-columns: 30px 2.5fr 1fr;
  }
}
.page__body--slide-1 {
  background-image: url("../images/promo-bg.svg"), url("../images/promo-bg-2.jpg");
  background-repeat: no-repeat;
  background-size: cover, 100%;
  background-position-y: top;
}
.page__body--slide-2 {
  background-image: none;
  background-image: url("../images/bg-left.svg");
  background-repeat: no-repeat;
  background-size: contain;
}
.page__body--slide-3 {
  background-image: none;
  background-image: url("../images/item-bg.svg");
  background-repeat: repeat;
  background-size: cover;
}
.page__body--slide-4 {
  background-image: none;
  background-image: url("../images/person-bg.png"), url("../images/bg-left.svg");
  background-repeat: no-repeat;
  background-size: contain, contain;
  background-position-y: bottom, top;
  background-position-x: right, left;
}
.page__body--slide-5 {
  background-image: none;
  background-image: url("../images/promo-bg.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position-x: right;
}

.page__main {
  grid-column: 2/-1;
  grid-row: 2/3;
}
.page__main--inner {
  grid-row: 2/3;
  grid-column: 2/3;
}

.header {
  z-index: 30;
  grid-column: 2/4;
  grid-row: 1/2;
  padding-right: 8.9%;
}

.header__wrapper {
  padding-top: 53px;
  padding-bottom: 10px;
  display: flex;
  position: relative;
}
@media (max-width: 767px) {
  .header__wrapper {
    padding-top: 27px;
  }
}

.logo {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.logo__link {
  margin-right: 58px;
}
@media (max-width: 767px) {
  .logo__link {
    max-width: 126px;
    margin-right: 30px;
  }
}

.lang {
  display: flex;
  align-items: center;
  margin-left: auto;
  gap: 24px;
}

.lang__link {
  font-size: 20px;
  line-height: 19px;
  color: #d2aa74;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .lang__link {
    font-size: 14px;
    line-height: 13px;
  }
}
.lang__link--active {
  color: #ffffff;
}

.main-nav__button .main-nav__icon {
  stroke: #333333;
}
.main-nav__button .main-nav__icon-cross {
  display: none;
}
.main-nav__button .main-nav__icon-menu {
  display: block;
}
.main-nav__button--open .main-nav__icon-cross {
  display: block;
}
.main-nav__button--open .main-nav__icon-menu {
  display: none;
}
@media (min-width: 1024px) {
  .main-nav__button {
    display: none;
  }
}

.main-nav__link {
  font-size: 12px;
  line-height: 11px;
  color: #ffffff;
  transition: all 0.3s ease;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  border-right: 1px solid #ffffff;
  width: 100%;
}
.main-nav__list--open {
  display: flex;
  flex-direction: column;
  row-gap: 50px;
}
.main-nav__list .swiper-pagination-bullet {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: auto;
  height: auto;
  border-radius: 0;
  background-color: transparent;
  opacity: 1;
  text-align: right;
}
.main-nav__list .swiper-pagination-bullet .main-nav__link {
  color: #707070;
}
.main-nav__list .swiper-pagination-bullet-active .main-nav__link {
  font-size: 20px;
  line-height: 19px;
  color: #ffffff;
}
@media (min-width: 1024px) {
  .main-nav__list {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    row-gap: 50px;
  }
}

.main-nav {
  display: flex;
  align-items: center;
  grid-column: 3/4;
  grid-row: 1/-1;
  padding-right: 23%;
  z-index: 20;
}

.main-nav__item {
  position: relative;
  padding-right: 23px;
  color: #ffffff;
}
.main-nav__item::after {
  content: "";
  position: absolute;
  right: -4px;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: 7px;
  height: 7px;
  background-color: #fff;
  border-radius: 50%;
}
.main-nav__item::before {
  content: "";
  position: absolute;
  right: -11px;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: 21px;
  height: 21px;
  border-radius: 50%;
  border: 1px solid #fff;
}

.promo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.promo__title {
  margin: 0;
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  font-size: 70px;
  line-height: 85px;
  font-weight: normal;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .promo__title {
    max-width: 630px;
    margin: 0 auto;
    margin-bottom: 20px;
    font-size: 150px;
    line-height: 0.8;
  }
}

.promo__text {
  margin: 0;
  margin-bottom: 17px;
  font-size: 14px;
  line-height: 13px;
}
@media (min-width: 768px) {
  .promo__text {
    margin-bottom: 30px;
    font-size: 20px;
    line-height: 19px;
  }
}

.promo__main-text {
  max-width: 425px;
  margin: 0 auto;
  font-size: 9px;
  line-height: 13px;
  /* or 144% */
  text-align: center;
}
@media (min-width: 768px) {
  .promo__main-text {
    max-width: 500px;
    font-size: 12px;
    line-height: 16px;
  }
}
.promo__main-text p {
  margin: 0;
}

.timeline-section__wrapper {
  max-width: 1440px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 100px;
  height: 100%;
}

.timeline__content {
  padding-bottom: 0;
}

.timeline__content-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.timeline__content-item {
  display: none;
  position: relative;
}
.timeline__content-item--active {
  display: block;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: left;
  background-position-y: top;
}

.timeline__line {
  position: relative;
  z-index: 2;
  transform: translateY(-100%);
  max-width: 1370px;
  margin-left: 2%;
}

.timeline__list-years {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.timeline__list-years::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  width: 100%;
  height: 3px;
  background: #fad398;
}

.timeline__list-year {
  position: relative;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 40px;
  max-width: 101px;
  /* identical to box height, or 133% */
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}
.timeline__list-year::after {
  content: "";
  position: absolute;
  bottom: -13px;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: 24px;
  height: 24px;
  border: 3px solid #fad398;
  border-radius: 50%;
  background-color: #1c1c1f;
  z-index: 2;
}
.timeline__list-year--active::after {
  width: 47px;
  height: 47px;
  bottom: -23px;
}

.timeline__year-btn {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  min-height: 50px;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  color: inherit;
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
}
.timeline__year-btn--active {
  font-size: 20px;
  line-height: 21px;
}

.timeline__article-wrapper img {
  margin: auto;
}

.timeline__article {
  z-index: 2;
  display: grid;
  place-items: flex-start;
  place-content: flex-start;
  grid-template-areas: "map";
}
.timeline__article h2 {
  grid-area: map;
  margin: 0;
  font-weight: normal;
  font-size: 20px;
  line-height: 22px;
  z-index: 2;
}
.timeline__article h2 span {
  text-transform: uppercase;
  display: block;
  width: 100%;
  font-size: 80px;
  line-height: 97px;
  font-family: "Cormorant Garamond", "Arial", sans-serif;
}

.timeline__dots-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.timeline__dot-item {
  width: 41px;
  height: 41px;
  background-color: #fad398;
  position: absolute;
}

.timeline__pins {
  position: relative;
  display: flex;
  grid-area: map;
}
.timeline__pins svg {
  max-width: 100%;
  height: auto;
}
.timeline__pins svg image {
  max-width: 100%;
  height: auto;
}

.timeline__pin-description {
  position: absolute;
  display: none;
}
.timeline__pin-description--active {
  display: block;
  padding: 35px;
  transform: translateY(calc(-100% + 20px)) translateX(20px);
  width: 267px;
  z-index: 1;
  background: rgba(255, 255, 255, 0.8);
  font-size: 20px;
  line-height: 23px;
  /* or 115% */
  color: #000000;
  border-bottom-left-radius: -10px;
}

.collections__catalog {
  display: grid;
  grid-template-columns: 1fr 1fr 1.2fr;
  gap: 2px;
  align-items: flex-start;
  max-width: 1000px;
}

.collections__column {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.collections__item {
  display: flex;
  justify-content: center;
  background-color: #1c1c1e;
  align-items: center;
  padding: 20px;
}
.collections__item--full {
  width: 100%;
}
.collections__item--half {
  width: calc(50% - 5px);
}
.collections__item--third {
  width: 32%;
}
.collections__item--six {
  width: 66%;
}
.collections__item a {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 20px;
}
.collections__item img {
  max-width: 100%;
  height: auto;
}

.collection__head {
  display: grid;
  grid-template-columns: max-content max-content;
  column-gap: 28px;
}

.collection__title {
  margin: 0;
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 40px;
  line-height: 48px;
  text-transform: uppercase;
  color: #ffffff;
  grid-column: 1/2;
}
@media (min-width: 1024px) {
  .collection__title {
    font-size: 80px;
    line-height: 97px;
  }
}

.collection__text {
  font-size: 10px;
  line-height: 12px;
  grid-column: 1/2;
}
@media (min-width: 768px) {
  .collection__text {
    font-size: 20px;
    line-height: 22px;
  }
}
.collection__text p {
  max-width: 490px;
}

.collection__table {
  grid-row: 1/3;
  grid-column: 2/3;
  padding-top: 30px;
  font-weight: 300;
  font-size: 14px;
  line-height: 23px;
}
.collection__table table tr td {
  font-weight: 400;
}
.collection__table table tr td:first-child {
  font-family: "gotham_prolight", "Arial", sans-serif;
}

.item__wrapper {
  position: relative;
}

.item__article {
  padding-left: 2%;
}

.item__article-header {
  display: grid;
  grid-template-columns: max-content max-content;
  align-items: center;
  column-gap: 28px;
  padding-top: 45px;
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  .item__article-header {
    padding-top: 20px;
    margin-bottom: 10px;
  }
}
@media (max-width: 735px) {
  .item__article-header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}
.item__article-header table {
  width: 100%;
  grid-column: 2/3;
  grid-row: 1/2;
}
.item__article-header table tr td:first-child {
  font-family: "gotham_prolight", "Arial", sans-serif;
}
@media (max-width: 1023px) {
  .item__article-header table {
    font-size: 8px;
    line-height: 14px;
  }
}

.item__article-name {
  margin: 0;
  grid-column: 1/2;
  grid-row: 1/2;
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  font-weight: 400;
  font-size: 80px;
  line-height: 65px;
}
@media (max-width: 1023px) {
  .item__article-name {
    font-size: 40px;
    line-height: 35px;
  }
}

.item__article-name-description {
  margin: 0;
  align-self: flex-start;
  grid-column: 1/-1;
  grid-row: 2/3;
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  font-size: 40px;
  line-height: 48px;
}
@media (max-width: 1023px) {
  .item__article-name-description {
    font-size: 14px;
    line-height: 17px;
    grid-column: 1/2;
  }
}

.item__article-content {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  max-width: 1040px;
  position: relative;
}
@media (max-width: 767px) {
  .item__article-content {
    grid-template-columns: 0.5fr 1fr;
  }
}
@media (max-width: 735px) {
  .item__article-content {
    display: flex;
    flex-direction: column;
  }
}

.item__article-thumbs {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
@media (max-width: 735px) {
  .item__article-thumbs {
    order: 2;
  }
}

.item__article-thumb {
  margin-right: 3px;
  padding: 10px;
  background-color: #1c1c1e;
}
@media (max-width: 735px) {
  .item__article-thumb {
    width: 23%;
  }
}
.item__article-thumb a {
  display: flex;
  justify-content: center;
  align-items: center;
}

.item__arcticle-text {
  align-self: center;
  padding-left: 3%;
  font-size: 16px;
  line-height: 22px;
}
.item__arcticle-text p {
  margin: 0;
}
@media (max-width: 767px) {
  .item__arcticle-text {
    font-size: 9px;
    line-height: 13px;
  }
}
@media (max-width: 735px) {
  .item__arcticle-text {
    order: 3;
  }
}

.item__article-full-photo {
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  .item__article-full-photo {
    margin-bottom: 20px;
  }
}
.item__article-full-photo a {
  display: flex;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 440px;
  padding: 0 20px;
}
@media (max-width: 767px) {
  .item__article-full-photo a {
    padding: 0;
    font-size: 9px;
    line-height: 13px;
    height: 200px;
  }
}
.item__article-full-photo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.back-page-nav {
  grid-column: 1/2;
  grid-row: 2/3;
  width: 52px;
  height: 363px;
  background-color: #d2aa74;
  margin: auto 8px auto auto;
}
.back-page-nav a {
  display: flex;
  align-items: center;
  height: 100%;
}
@media (max-width: 1023px) {
  .back-page-nav {
    width: 30px;
    height: 208px;
  }
}

.person__wrapper {
  display: grid;
  grid-template-columns: max-content max-content;
}

.person__title {
  margin: 0;
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 40px;
  line-height: 48px;
  text-transform: uppercase;
  color: #ffffff;
}
@media (min-width: 1024px) {
  .person__title {
    font-size: 80px;
    line-height: 97px;
  }
}

.person__years {
  margin: 0;
  margin-left: 10px;
  padding-top: 20px;
  font-size: 20px;
  line-height: 22px;
}

.person__description {
  grid-column: 1/-1;
  width: 100%;
  padding-top: 16px;
  max-width: 920px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (min-width: 768px) {
  .person__description {
    padding-top: 70px;
    grid-template-columns: max-content 410px;
    column-gap: 10%;
  }
}

@media (min-width: 768px) {
  .person__text {
    height: 500px;
    overflow-y: scroll;
  }
  .person__text::-webkit-scrollbar {
    width: 10px;
    border: 1px solid #bd9869;
  }
  .person__text::-webkit-scrollbar-track {
    background-color: transparent;
  }
  .person__text::-webkit-scrollbar-thumb {
    background: #bd9869;
  }
}

.video-section .swiper-container {
  max-width: 920px;
  margin: 0;
}
.video-section .swiper-pagination {
  position: static;
}
.video-section .swiper-pagination .swiper-pagination-bullet {
  width: 23px;
  height: 23px;
  margin-top: 13;
  border: 1px solid #ffd89b;
  background-color: transparent;
  opacity: 1;
}
.video-section .swiper-pagination .swiper-pagination-bullet-active {
  background-color: #ffd89b;
}
@media (min-width: 768px) {
  .video-section .swiper-pagination .swiper-pagination-bullet {
    margin-top: 22px;
  }
}

.gallery-thumbs .swiper-slide {
  height: 100%;
  opacity: 1 !important;
}

.video {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  background-color: #000000;
}

.video__link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video__media,
iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.video__button {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  display: none;
  padding: 0;
  width: 105px;
  height: 105px;
  border: none;
  background-color: transparent;
  transform: translate(-50%, -50%);
  cursor: pointer;
}
.video__button svg {
  max-width: 100%;
  height: auto;
}
@media (min-width: 768px) {
  .video__button {
    width: 188px;
    height: 188px;
  }
}

.video__button-shape:hover {
  opacity: 1;
}

.video__button-icon {
  fill: #ffffff;
}

.video__button:focus {
  outline: none;
}

/* Enabled */
.video--enabled {
  cursor: pointer;
}

.video--enabled .video__button {
  display: block;
}