.person {
}
.person__wrapper {
  display: grid;
  grid-template-columns: max-content max-content;
}

.person__title {
  @include title;
}

.person__years {
  margin: 0;
  margin-left: 10px;
  padding-top: 20px;
  font-size: 20px;
  line-height: 22px;
}

.person__description {
  grid-column: 1/-1;
  width: 100%;
  padding-top: 16px;
  max-width: 920px;
  display: grid;
  grid-template-columns: 1fr 1fr;

  @media (min-width: 768px) {
    padding-top: 70px;
    grid-template-columns: max-content 410px;
    column-gap: 10%;
  }
}

.person__text {
  @media (min-width: 768px) {
    height: 500px;
    overflow-y: scroll;
    &::-webkit-scrollbar {
      width: 10px;
      border: 1px solid #bd9869;
    }

    &::-webkit-scrollbar-track {
      background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
      background: #bd9869;
    }
  }
}
